html {
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

body {
    margin: 0;
    font-family: "Roboto", sans-serif;
    background-color: #fff;
}

img {
    max-width: 100%;
    height: auto;
}

a {
    text-decoration: none;
    transition: all .3s;
}

.container {
    margin: 0 auto;
    max-width: 1168px;
    min-width: 288px;
    padding: 0 24px;
}

@media only screen and (max-width: 1152px) {
    .container {
        padding: 0 16px;
    }
}
